body {
  font-family: PFHandbookPro, sans-serif;
  font-weight: 300;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 0em;
  color: #333333;
  min-height: 8000px;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
}

.container {
  max-width: 1500px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.button {
  border-radius: 3px;
  padding: 18px 42px 18px 42px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}

.button_pink {
  background-color: #EF4D9C;
  color: #FFF;
}

.button_zero {
  background-color: transparent;
  border: 1px solid #333333;
  color: #333333;
}

.button_grey {
  background-color: #9B9B9C;
  color: #FFF;
}

.title {
  font-weight: 500;
  font-size: 56px;
  line-height: 64px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  cursor: default;
}

.vertical-panel {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: absolute;
  z-index: 10;
  top: -1%;
  left: 0;
  padding-top: 1%;
  height: 102%;
  width: 125px;
  background-color: #fff;
}

.vertical-panel__title {
  color: #565658;
  font-size: 40px;
  line-height: 48px;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  height: 120px;
  background: rgba(255, 255, 255, 0.82);
  backdrop-filter: blur(5px);
  border-bottom: 1px solid #E0E0E0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__phone {
  font-size: 24px;
  line-height: 32px;
}

.first-nav__list {
  display: flex;
  gap: 40px;
}

.first-nav__link {
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
}

.main__mainslider {
  margin-bottom: 136px;
}

.main__about {
  margin-bottom: 136px;
}

.main__catalog {
  margin-bottom: 136px;
}

.main__news {
  margin-bottom: 136px;
}

.main__specialists {
  margin-bottom: 136px;
}

.mainslider {
  height: 884px;
  background-color: #F8F8F8;
}

.about {
  position: relative;
}

.about__container {
  position: relative;
  z-index: 10;
}

.about__title {
  margin-bottom: 48px;
}

.about__body {
  display: flex;
  justify-content: space-between;
}

.about__left-column {
  flex: 0 0 35%;
}

.about__line {
  width: 42px;
  height: 70px;
  border-right: 1px dashed #565658;
  margin: 8px 0;
}

.about__right-column {
  flex: 0 0 57%;
}

.about__description {
  margin-bottom: 40px;
}

.about__description_l {
  font-size: 28px;
  line-height: 36px;
  margin-bottom: 40px;
}

.about__description_s {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 30px;
}

.about__description_s:last-child {
  margin-bottom: 0;
}

.about__figure {
  position: absolute;
  z-index: 1;
}

.about__figure_01 {
  top: 0;
  left: 40%;
}

.about__figure_02 {
  top: 45%;
  right: 0;
  transform: translateX(50%);
}

.about__figure_03 {
  bottom: -6%;
  right: 15%;
}

.dignity {
  display: flex;
  align-items: center;
}

.dignity__image {
  width: 84px;
  margin-right: 44px;
}

.dignity__title {
  flex: 0 1 70%;
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
}

.catalog {
  position: relative;
  z-index: 9;
}

.catalog__card {
  width: 100%;
}

.prod-card {
  height: 368px;
  overflow: hidden;
  margin-bottom: 2px;
}

.prod-card_green {
  background-color: #A3E07E;
  transition: 300ms;
}

.prod-card_yellow {
  background-color: #FDED84;
  transition: 300ms;
}

.prod-card_pink {
  background-color: #FF9CCA;
  transition: 300ms;
}

.prod-card_orange {
  background-color: #FDB96B;
  transition: 300ms;
}

.prod-card__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.prod-card__info {
  flex: 0 0 38%;
}

.prod-card__icon {
  margin-bottom: 24px;
}

.prod-card__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: 0.01em;
  margin-bottom: 24px;
  cursor: default;
}

.prod-card__description {
  cursor: default;
}

.prod-card__image {
  transform: translateY(22%);
  transition: 300ms;
}

.prod-card__btn {
  transition: 300ms;
}

.news {
  position: relative;
}

.news__container {
  position: relative;
  z-index: 10;
}

.news__title {
  margin-bottom: 48px;
}

.news__body {
  display: flex;
  justify-content: space-between;
}

.news__card {
  flex: 0 0 32%;
}

.news__figure {
  position: absolute;
  z-index: 1;
}

.news__figure_01 {
  bottom: -10%;
  left: 40%;
}

.news__figure_02 {
  top: 35%;
  right: 0;
  transform: translateX(50%);
}

.news__figure_03 {
  top: -6%;
  right: 17%;
}

.n-card__image {
  margin-bottom: 36px;
  overflow: hidden;
}

.n-card__image img {
  transition: 300ms;
}

.n-card__data {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #71BD44;
}

.n-card__title {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 32px;
  line-height: 32px;
  transition: 300ms;
}

.specialists {
  position: relative;
  height: 738px;
  border: 1px solid #000;
}

.partnersslider {
  height: 404px;
  background-color: #F8F8F8;
  border: 1px solid #000;
}

.footer {
  background-color: #565658;
  color: #FFF;
}

.footer__container {
  padding: 115px 0;
}

.footer__body {
  display: flex;
  height: 204px;
  margin-bottom: 102px;
}

.footer__subscribe {
  flex: 0 0 50%;
}

.footer__nav {
  flex: 0 0 50%;
  border: 1px solid #000;
}

.footer__info {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  line-height: 24px;
  cursor: default;
}

.footer__years {
  font-weight: 500;
}

.footer__madefrom {
  text-transform: uppercase;
}

.subscribe__description {
  width: 405px;
  margin-bottom: 25px;
  font-size: 19px;
  line-height: 24px;
  cursor: default;
}

.subscribe__form {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.subscribe__input {
  width: 421px;
  height: 60px;
  padding: 18px 22px;
  border: 1px solid #FFF;
  margin-right: 14px;
}

.subscribe__input::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  color: #FFF;
}

.social {
  display: flex;
}

.social__link {
  width: 40px;
  height: 40px;
  border: 1px solid #FFF;
  border-radius: 50%;
  margin-right: 16px;
  overflow: hidden;
  transition: 300ms;
}

.social__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.social__btn img {
  display: block;
}

.second-nav__list {
  column-width: 200px;
}

.feedback {
  position: fixed;
  z-index: 50;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 684px;
  padding-top: 106px;
  background-color: #FFF;
  border-left: 1px solid #E0E0E0;
  transform: translateX(684px);
  transition: 400ms;
}

.feedback__title {
  margin-bottom: 90px;
  font-weight: 700;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: 0.01em;
  cursor: default;
}

.feedback__form {
  width: 382px;
}

.feedback__label {
  opacity: 0;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.03em;
  color: #959595;
}

.feedback__label_required::after {
  content: "*";
  color: #EF4D9C;
}

.feedback__input {
  width: 100%;
  height: 40px;
  margin-bottom: 40px;
  border-bottom: 2px solid #565658;
  font-weight: 300;
  font-size: 28px;
  line-height: 36px;
  letter-spacing: 0.03em;
  color: #333333;
}

.feedback__input::placeholder {
  color: #959595;
}

.feedback__agreement {
  display: flex;
  margin-bottom: 40px;
}

.feedback__checkbox {
  display: none;
}

.feedback__checkbox:checked + .feedback__castom-checkbox .feedback__check {
  display: block;
}

.feedback__castom-checkbox {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-right: 25px;
  border: 1px solid #565658;
  border-radius: 3px;
}

.feedback__check {
  display: none;
}

.feedback__description {
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.03em;
  cursor: default;
}

.feedback__btn {
  margin-bottom: 36px;
}

.feedback__note {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.03em;
  cursor: default;
}

.feedback__note::before {
  content: "* ";
  color: #EF4D9C;
}

.feedback__ticket {
  position: absolute;
  background-color: #F9931F;
  width: 48px;
  color: #FFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.feedback__ticket_max {
  top: 20%;
  left: -48px;
  height: 188px;
  border-radius: 3px 0% 0% 3px/3px 0% 0% 3px;
  filter: drop-shadow(-2px 0px 6px rgba(0, 0, 0, 0.2));
}

.feedback__ticket_max span {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.feedback__ticket_min {
  top: 50%;
  left: 0;
  height: 56px;
  border-radius: 0% 3px 3px 0%/0% 3px 3px 0%;
  transform: translateY(-50%);
}

.feedback__ticket_min img {
  display: block;
}

@media (min-width: 992px) {
  .prod-card:hover .prod-card__image {
    transform: translateY(13%);
  }

  .prod-card:hover .prod-card__btn {
    background-color: #333333;
    color: #FFF;
  }

  .prod-card_green:hover {
    background-color: #71BD44;
  }

  .prod-card_yellow:hover {
    background-color: #FFDD00;
  }

  .prod-card_pink:hover {
    background-color: #F669B8;
  }

  .prod-card_orange:hover {
    background-color: #F9931F;
  }

  .n-card:hover .n-card__title {
    color: #EF4D9C;
  }

  .n-card:hover .n-card__image img {
    transform: scale(1.1);
  }

  .social__link:hover {
    border: 1px solid transparent;
    background-color: #EF4D9C;
  }
}